import cuModal from "@/meetu-ui/components/cu-modal.vue";
export default {
	name: 'homeIndex',
	components: {
		cuModal
	},
	data() {
		return {
			modalName: '',
			appName: 'Meet U',
			userInfo: {
				avatar: '../../static/logo.png',
				usenum: 4,
				total: 6
			},
			showSendToast: false,
		}
	},
	onReady() {
		if(uni.getStorageSync('homeSendToast')) {
			this.showSendToast = false;
		} else {
			this.showSendToast = true;
		}
	},
	methods: {
		linkUser() {
			uni.navigateTo({
				url: '../user/index',
				animationDuration: 300,
				animationType: 'slide-in-bottom'
			})
		},
		linkAction(type) {
			switch (type){
				case 'search':
					uni.navigateTo({
						url: '../search/search',
						animationDuration: 300,
						animationType: 'slide-in-bottom'
					})
					break;
				case 'send':
					if(!uni.getStorageSync('homeSendToast')) {
						uni.setStorageSync('homeSendToast', true);
						this.showSendToast = false;
					} 
					this.modalName = 'bottomModal';
					break;
				case 'chatList':
					uni.navigateTo({
						url: '../chat/list',
						animationDuration: 300,
						animationType: 'slide-in-bottom'
					})
					break;
				case 'sendText':
					this.modalName = '';
					uni.navigateTo({
						url: '../send/text',
						animationDuration: 300,
						animationType: 'slide-in-bottom'
					})
					break;
				case 'sendVoice':
					this.modalName = '';
					uni.navigateTo({
						url: '../send/voice',
						animationDuration: 300,
						animationType: 'slide-in-bottom'
					})
					break;
			}
		},
		hideModal() {
			this.modalName = '';
		}
	}
}
